.compra{
    width: 100vw;
    max-width: 1200px;
}

.compra hr{
    border: none;
    height: 1px;
    color: #E0E0E0;
    background-color: #E0E0E0;
    border: 1px solid ;
    width: 100%;
    margin: 10px;
}

.compra h3{
    font-weight: 400;
}

.compra h4{
    font-weight: 300;
}

.compra__sub-titulo{
    display: flex;
    flex-wrap: wrap;
    row-gap: 5px;
    width: 100%;
    justify-content: space-between;
    align-items: center;
}

@media only screen and (max-width: 600px) {
    .compra__sub-titulo{
        display: flex;
        width: 100%;
        justify-content: space-between;
        align-items: center;
    }

    .carrito-validar__table-cont{
        padding: 0px;
    }
  }